const EPSILOW = 1e-8;

/**
 *  ||u|| u
 *  vector + vector  [1,2]+[3,4] = [4,6]
 *  vector - vector  [1,2]+[3,4] = [-2,-2]
 *  vector * k  [1,2] * 3 = [3,6]
 *  vector * vector k[1,2]  * [3,4] =1*3 +2 * 4 = 11
 *      ----------------------------------------------------
 *      - 
 *      - k > 0 
 *      - k < 0 
 *      - k ==0  
 *      ----------------------------------------------------
 *      - 
 *      -   cos = (v1 * v2 ) /||v1|| * ||v2||  :number
 *      -  d = (v1 * v2 ) / ||v2|| = cos* ||v1||  : number
 *      -  P = d * v2.nonamlize()   :Vector
 *  norm :    v[0]^2 +v[1]^2 +...v[n]^2 :number
 *  normalize:  v * 1/norm :Vector
 */
class Vector {
  static zero(dim) {
    return new Vector(new Array(dim).fill(0));
  }

  constructor(lst) {
    this._values = lst;
  }

  valueOf() {
    return JSON.parse(JSON.stringify(this._values));
  }

  /**
   * 
   *  ||a^2+b^2+c^2+...n^2||
   * @return {number}
   */
  norm() {
    return Math.sqrt(this._values.map((item) => Math.pow(item, 2)).reduce((a, b) => a + b));
  }

  /**
   *,  *  
   * @return {Vector}
   */
  normalize() {
    let n = this.norm();
    if (n < EPSILOW) throw Error(`[ Normalize Error] ${this.toString()} can not be normalize.`);
    return this.div(n);
  }

  toString() {
    return `Vector(${this._values.join(', ')})`;
  }

  get length() {
    return this._values.length;
  }

  // 
  /**
   * 
   * @param vector
   * @return {Vector}
   */
  add(vector) {
    return new Vector(vector._values.map((item, index) => item + this._values[index]));
  }

  /**
   * 
   * @param vector
   * @return {Vector}
   */
  sub(vector) {
    return new Vector(vector._values.map((item, index) => item - this._values[index]));
  }

  /**
   *  
   * @param number
   * @return {Vector}
   */
  mul(number) {
    return new Vector(this._values.map((item) => item * number));
  }

  /**
   * 1. 
   *  - =0 ,  ,
   *  - >0 , , 
   *  - <0 , 
   * 2.
   *  -  dot() / ||vector||
   *  -  (dot() / ||vector||) :number
   *  -  (dot() / ||vector||) * normalize() :Vector
   *
   * @param vector
   * @return {number}
   */
  dot(vector) {
    if (vector instanceof Vector && this.length === vector.length) {
      return this._values.map((item, index) => item * vector._values[index]).reduce((a, b) => a + b);
    }
    throw Error('The param `vector` must instance of Vector');
  }

  // 
  div(number) {
    return this.mul(1 / number);
  }

  // 
  pos() {
    return this.mul(1);
  }

  // 
  neg() {
    return this.mul(-1);
  }

  get(index) {
    return this._values[index];
  }
}

export default Vector;
